import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { GetChemistByAreaComponent } from './GetChemistByArea.component';
import '../../assets/jquery-locationpicker-plugin-master/src/locationpicker.jquery.js';

export const routes = [
  { path: '', component: GetChemistByAreaComponent, pathMatch: 'full' }
];

@NgModule({
  declarations: [
    GetChemistByAreaComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    RouterModule.forChild(routes),
    
  ]
})
export default class GetChemistByAreaModule {
  static routes = routes;
}



